html,
body {
  font-size: 16px;
}
/* font size resets */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700!important;
}
h1 {
  font-size: 2em;
}
h2 {
  font-size: 1.75em;
}
h3 {
  font-size: 1.5em;
}
h4 {
  font-size: 1.25em;
}
h5 {
  font-size: 1em;
}
h6 {
  font-size: 0.75em;
}
/* ng common */
.p-app .gpLoginCover {
  position: absolute;
}
/* -------- ANCHORS ------------------ */
a:not(.btn),
a:visited:not(.btn) {
  color: #2079c5;
}
a.selected:not(.btn) {
  color: #fff;
}
a.btn.btn-dark,
a.btn.btn-dark:visited {
  color: #fff;
}
a[ng-click],
[onclick],
[ng-click],
a[ui-sref] {
  cursor: pointer;
}
/* -------- FORMS ------------------ */
body .form-control.form-control-lg {
  padding: 0.65em 1em;
  font-size: 1.25em;
  line-height: 1.5;
  border-radius: 0.3em;
  height: auto;
}
/* ------- INPUT GROUP ------------- */
.input-group .input-group-btn {
  font-size: 1em;
}
.input-group-slick > .icon:first-child {
  left: 0.75em;
}
.input-group-slick > .icon {
  position: absolute;
  top: 0.625em;
  color: #848484;
  height: 1em;
  width: 1em;
}
.input-group-slick.input-group-slick--lg {
  font-size: 1.2875em;
}
.input-group-slick.input-group-slick--lg > .icon {
  top: 0.9em;
}
/* -------- BOOTSTRAP DROPDOWNS -------- */
/*
 * Hack necessary to enable bootstrap dropdown-contained links work on touch devices
 * http://stackoverflow.com/questions/17579750/drop-down-menu-not-working-on-mobile-devices
 */
.dropdown-backdrop {
  position: static;
}
/* -------- BOOTSTRAP TABS -------- */
ul.nav.nav-tabs > li.active {
  background: transparent;
}
.tab-pane.active {
  background: none;
  color: inherit;
}
/* -------- BUTTONS --------------- */
.btn.btn-primary {
  background-color: #2079c5;
  border-color: #2079c5;
}
.btn.btn-info {
  background-color: #007fa4;
  border-color: #007fa4;
}
/* Bootstrap 4 list group overrides */
a.list-group-item.active,
a.list-group-item.active:visited {
  color: #fff;
}
/* bootstrap 4 doesn't bold labels */
.form-group label {
  font-weight: 500;
  font-size: 1.125em;
}
/* ui bootstrap typeahead dropdown wrapping of long values */
.uib-typeahead-match .u-break--all {
  white-space: pre-wrap;
}
/* ui-bootstrap for bs4 overrides for datepicker button colors */
.uib-day button.btn-secondary:not(:disabled) {
  background-color: #fff;
  color: #333;
}
.uib-day button.btn-secondary:not(:disabled).active,
.uib-day button.btn-secondary:not(:disabled).active:focus {
  background-color: #ddd;
  border-color: blue;
  box-shadow: none;
}
