/*
 Embedded items (images, iframes, videos, etc) with aspect ratios preserved

 ex: <div class="a-embed u-16x9">
       <img src="...">
     </div>
 */
.a-embed {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;
}
.a-embed::before {
  display: block;
  content: "";
  padding-top: 100%;
}
.a-embed.u-16x9::before {
  padding-top: 56.25%;
}
.a-embed.u-4x3::before {
  padding-top: 75%;
}
.is-4x3 > :first-child,
.is-16x9 > :first-child {
  width: 100%;
}
.is-16x9 > img {
  height: auto;
}
@supports (--custom:property) {
  .is-4x3,
  .is-16x9 {
    position: relative;
  }
  .is-4x3::before,
  .is-16x9::before {
    content: "";
    display: block;
  }
  .is-4x3::before {
    padding-bottom: calc(100% / (4/3));
  }
  .is-16x9::before {
    padding-bottom: calc(100% / (16/9));
  }
  .is-4x3 > :first-child,
  .is-16x9 > :first-child {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
  }
}
