.gp-ui-card__tab.active:after {
  bottom: 76%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: rgba(51, 153, 51, 0);
  border-bottom-color: #bbb;
