/* You can add global styles to this file, and also import other style files */
.a-keyword {
  display: inline-block;
  font-size: 0.75em;
  padding: 0.25em 0.5em;
  margin: 0 0.5em 0.25em 0;
  border-radius: 8px;
  background: #ddd;
  color: #747474;
}
.a-message--warning {
  padding: 0.5em 0.75em;
  margin-bottom: 1em;
  border-radius: 4px;
  color: #fff;
  background-color: #b35d29;
}
.m-message--error a:not(.btn),
.m-message--error a:visited:not(.btn) {
  color: #fff;
  text-decoration: underline;
}
